.browsehappy {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

body > main {
  padding-top: 80px;
}

.table-header-rotated > thead > tr, .table-header-rotated {
  border: none;
}

.table-header-rotated > thead > tr > th {
  height: 100px;
  width: 40px;
  padding: 0;
  border: none;
}

.table-header-rotated thead th > div {
  position: relative;
  height: 100%;
  top: 0;
  /* 100 * tan(45) / 2 = 40 where 100 is the height on the cell and 45 is the transform angle */
  left: 50px;
  -ms-transform:skew(-45deg,0deg);
  -moz-transform:skew(-45deg,0deg);
  -webkit-transform:skew(-45deg,0deg);
  -o-transform:skew(-45deg,0deg);
  transform:skew(-45deg,0deg);
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}

.table-header-rotated thead th span {
  -ms-transform: skew(45deg, 0deg) rotate(315deg);
  -moz-transform: skew(45deg, 0deg) rotate(315deg);
  -webkit-transform: skew(45deg, 0deg) rotate(315deg);
  -o-transform: skew(45deg, 0deg) rotate(315deg);
  transform: skew(45deg, 0deg) rotate(315deg);
  position: absolute;
  /* 50 cos(45) = 35 with an additional 2px margin*/
  bottom: 37px;
  /* Because it looked good, but there is probably a mathematical link here as well */
  left: -40px;
  /* 100 / cos(45) - 40 cos (45) = 85 where 80 is the height of the cell, 40 the width of the cell and 45 the transform angle*/
  width: 113px;
  white-space: nowrap;
}

.unstyle-anchor {
  color: inherit;
}

.cursor-pointer {
  cursor: pointer;
}

/* Fix navbar moving to the right when dialog is open */
.ngdialog-open .navbar {
  padding-right: 15px;
}

.ngdialog.ngdialog-theme-plain {
  padding-bottom: 80px;
  padding-top: 80px;
}

.ngdialog.ngdialog-theme-plain .ngdialog-content {
  max-width: 750px;
  padding: 1em;
  position: relative;
}

.ngdialog.ngdialog-theme-plain .ngdialog-content h1,
.ngdialog.ngdialog-theme-plain .ngdialog-content h2,
.ngdialog.ngdialog-theme-plain .ngdialog-content h3,
.ngdialog.ngdialog-theme-plain .ngdialog-content h4,
.ngdialog.ngdialog-theme-plain .ngdialog-content h5,
.ngdialog.ngdialog-theme-plain .ngdialog-content h6,
.ngdialog.ngdialog-theme-plain .ngdialog-content p,
.ngdialog.ngdialog-theme-plain .ngdialog-content ul,
.ngdialog.ngdialog-theme-plain .ngdialog-content li {
  color: inherit;
}

.ngdialog.ngdialog-theme-plain .ngdialog-close {
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 0;
}

.ngdialog.ngdialog-theme-plain .ngdialog-close:before {
  background: transparent;
  color: #bbb;
  content: "\00D7";
  font-size: 26px;
  font-weight: 400;
  height: 30px;
  line-height: 26px;
  position: absolute;
  right: 3px;
  text-align: center;
  top: 3px;
  width: 30px;
}

.ngdialog.ngdialog-theme-plain .ngdialog-close:hover:before,
.ngdialog.ngdialog-theme-plain .ngdialog-close:active:before {
  color: #777;
}

.ngdialog.ngdialog-theme-plain .ngdialog-message {
  margin-bottom: .5em;
}

.ngdialog.ngdialog-theme-plain .ngdialog-input {
  margin-bottom: 1em;
}

.ngdialog.ngdialog-theme-plain .ngdialog-input textarea,
.ngdialog.ngdialog-theme-plain .ngdialog-input input[type="text"],
.ngdialog.ngdialog-theme-plain .ngdialog-input input[type="password"],
.ngdialog.ngdialog-theme-plain .ngdialog-input input[type="email"],
.ngdialog.ngdialog-theme-plain .ngdialog-input input[type="url"] {
  background: #f0f0f0;
  border: 0;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  margin: 0 0 .25em;
  min-height: 2.5em;
  padding: .25em .67em;
  width: 100%;
}

.ngdialog.ngdialog-theme-plain .ngdialog-input textarea:focus,
.ngdialog.ngdialog-theme-plain .ngdialog-input input[type="text"]:focus,
.ngdialog.ngdialog-theme-plain .ngdialog-input input[type="password"]:focus,
.ngdialog.ngdialog-theme-plain .ngdialog-input input[type="email"]:focus,
.ngdialog.ngdialog-theme-plain .ngdialog-input input[type="url"]:focus {
  box-shadow: inset 0 0 0 2px rgba(0,0,0,0.2);
  outline: none;
}

.ngdialog.ngdialog-theme-plain .ngdialog-buttons:after {
  clear: both;
  content: '';
  display: table;
}

.ngdialog.ngdialog-theme-plain .ngdialog-button {
  border: 0;
  cursor: pointer;
  float: right;
  font-family: inherit;
  font-size: .8em;
  letter-spacing: .1em;
  line-height: 1em;
  margin: 0 0 0 .5em;
  padding: .75em 2em;
  text-transform: uppercase;
}

.ngdialog.ngdialog-theme-plain .ngdialog-button:focus {
  -webkit-animation: ngdialog-pulse 1.1s infinite;
  animation: ngdialog-pulse 1.1s infinite;
  outline: none;
}

@media (max-width: 568px) {
  .ngdialog.ngdialog-theme-plain .ngdialog-button:focus {
    -webkit-animation: none;
    animation: none;
  }
}

.ngdialog.ngdialog-theme-plain .ngdialog-button.ngdialog-button-primary {
  background: #3288e6;
  color: #fff;
}

.ngdialog.ngdialog-theme-plain .ngdialog-button.ngdialog-button-secondary {
  background: #e0e0e0;
  color: #777;
}

.match-view .match-card-red {
  background-color: red;
}

.match-view .match-card-yellow {
  background-color: yellow;
}

.match-view .match-best-player {
  color: blue;
  font-weight: bold;
}

#matches-table-view .table-responsive {
  overflow-x: hidden;
  overflow-y: inherit;
  border: none;
}

#matches-table-view table tbody th {
  white-space: nowrap;
}

#matches-table-view tbody > tr > td {
  text-align: center;
}

#matches-table-view tbody > tr > td:last-child {
  border-right: 2px solid #ddd;
}

#matches-table-view .strike-through {
  background-color: #eaeaea;
}

@media screen and (max-width: 767px) {
  #matches-table-view .table-responsive {
    overflow-x: inherit;
  }

  #matches-table-view tbody > tr > th:first-child {
    border-left: 1px solid #ddd;
  }

  #matches-table-view tbody > tr > td:last-child {
    border-right: 1px solid #ddd;
  }

  #matches-table-view tbody > tr:last-child > td, #matches-table-view tbody > tr:last-child > th {
    border-bottom: 1px solid #ddd;
  }
}
